@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=UnifrakturCook:wght@700&display=swap');
html, body, a, button, p,b{
  all: unset;
}
button{
  cursor: pointer;
}
body{
  
  background-color: #ddd;
}        
.mainContainer{
          display: flex;
          width: 100%;
          height: 90vh;
          overflow-y: hidden;
          overflow-x: hidden;
          background-color: #ddd;
          align-items: center;
          justify-content: center;
          position: relative;
          font-family: 'Poppins', sans-serif;
        }
        .insideElement{
          display: flex;
          position: relative;
            align-items: center;
            justify-content: space-evenly;
            height: 90%;
            width: 80%;
            background-color: #fff;
            border-radius: 14px;
            flex-direction: column;
            box-shadow: 0 5px 4px 1px  #0003;
        }
        .OutsideLights{
          display: flex;
          flex-direction: column;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            height: 150px;
        }
        .button{
          display: flex;
            align-items: center;
            justify-content: center;
            width: 80%;
            border: 1px solid #f90;
            border-radius: 100px;
            padding: 17px;
            border-width: 2px;
        }
        .buttonBusy{
            background-color: #f90;
            border-color: #f90;
            color:#fff;
            
        }
        .buttonBusyActivated{
            background-color: #fff;
            border-color: #f90;
        }
        .buttonSleep{
            background-color: #44d;
            color:#fff;
            border-color: #44d;
        }
        .buttonSleepActivated{
            background-color: #fff;
            border-color: #44d;
        }
        .buttonBusyText{
            color: #fff;
            font-size: 24px;
        }
        .buttonBusyTextActivated{
            color: #f90;
            font-size: 24px;
        }
        .buttonSleepText{
            color: #fff ;
            font-size: 24px;
        }
        .buttonSleepTextActivated{
            color: #44d;
            font-size: 24px;
        }

        .doorOpen{
            color: #8f8;
            font-size: 20px;
            font-weight: 900;
        }
        .doorClosed{
            color: #f88;
            font-size: 20px;
            font-weight: 900;
        }

        .shutter{
          display: flex;
            flex-direction: row;
            width: 100%;
            justify-content: space-evenly;
            align-items: flex-start;
        }
        .shutterText{
            font-size: 20px;
            color:  #999;
        }
        .shutterState{
            border-radius: 12px;
            background-color:  #30f7;
            margin-top: 20px;
            text-align: center;
            padding: 10px;
            color: #fff;
        }
        .shutterUp{
            border-radius: 200px;
            padding: 24px;
            background-color: #af9a;
        }
        .shutterUp:hover{
          background-color: #af9e;
        }
        .shutterDown{
            border-radius: 200px;
            padding: 24px;
            background-color: #7dca;
        }
        .shutterDown:hover{
          background-color: #7dce;

        }
        .shutterButtonText{
            font-size: 40px;
            color: #fff;
        }
        .refresh{
            border-width: 2px;
            width: 53px;
            height: 53px;
            border: 2px solid #aaa;
            border-radius: 15px;
            margin: 20px;
            position: absolute;
            top: -10px;
            right: -10px;
            font-size: 30px;
            text-align: center;
            color: #aaa;
        }
        .refresh:hover{
          background-color: #aaa;
          color: #fff;
        }
        .refresh-text{
          font-size: 35px;
          text-align: 'center';
        }